.details{
    margin: 4rem 6rem;
}

.details h1{
    font-size: 3rem;
}

.first-det{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 4.5rem;
}

.first-det-reverse{
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    margin-top: 4.5rem;
}

.details-text{
    width: 45%;
    text-align: start;
    font-size: 1.1rem;
}

.details-text h2 {
    padding-bottom: 1rem;
}

.details-image{
    position: relative;
    width: 50%;
    display: flex;
    justify-content: space-between;
    z-index: -99;
}

.details-image img{
    width: 49%;
    height: 350px;
    object-fit: cover;
    border-radius: 6px;
    box-shadow: -1px 1px 62px -18px rgba(0, 0, 0, 0.19);
}

.details-image img:nth-child(2){
    position: absolute;
    top: -10%;
    right: 0;
}

@media screen and (max-width: 850px) {
    .details{
        margin: 4rem 2rem;
    }

    .first-det{
        display: flex;
        flex-direction: column-reverse;
        margin-top: 2.5rem;
    }

    .first-det-reverse{
        display: flex;
        flex-direction: column-reverse;
        margin-top: 4.5rem;
    }

    .details-text{
        width: 100%; 
    }

    .details-image{
        width: 100%;
        margin: 1.8rem 0;
    }

    .details-image img {
        height: 250px;
    }
}